import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ProductsRoutingModule } from './products-routing.module';
import { AllProductsComponent } from './all-products/all-products.component';
import { ProductDetailsComponent } from './product-details/product-details.component';
import { CoreModule } from 'src/app/core/core.module';
import { SlickCarouselModule } from 'ngx-slick-carousel';
import { CustomerDetailsComponent } from './customer-details/customer-details.component';
import {ProductService} from '../../../services/product.service';
import { ProductSliderImgComponent } from './product-slider-img/product-slider-img.component';
import {MatDialogModule} from '@angular/material/dialog';
import {MatIconModule} from "@angular/material/icon";


@NgModule({
  declarations: [AllProductsComponent, ProductDetailsComponent, CustomerDetailsComponent, ProductSliderImgComponent],
  imports: [
    CommonModule,
    ProductsRoutingModule,
    CoreModule,
    SlickCarouselModule,
    MatDialogModule,
    MatIconModule
  ],
  entryComponents:[
    ProductSliderImgComponent
  ],
  providers:[
    ProductService
  ]
})
export class ProductsModule { }
